






















































import {
    Vue
} from 'vue-property-decorator'
import SignupTxt from '@/components/molecules/Login/Signup_txt.vue'


export default Vue.extend({
    name: "signup",
    components: {
        SignupTxt
    },
    data() {
        return {
            userExists: false,
            name: '',
            email: '',
            password: '',
            password2: '',
            agree: '',
            loading: false,
            show1: false,
            show2: false,
            alert: false,
            rules: {
                required: (value: never) => {
                    return !!value || "Required"
                },
                agree: (value: boolean) => {
                    return value === true
                },
                email: (value: string) => {
                    const pattern =
                        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                    return pattern.test(value) || "Please enter a valid Email.";
                },
                password: (value: string) => {
                    // eslint-disable-next-line no-useless-escape
                    const pattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*().,])(?=.{8,})/;
                    return (
                        pattern.test(value) ||
                        "Min. 8 characters with at least one capital letter, a number and a special character (!@#$%^&*().,)"
                    );
                },
                confirm_password: (password: string, password2: string) => {
                    if (password !== password2) {
                        return "Confirmation Password is not match"
                    }
                    return true
                }
            }
        }
    },
    methods: {
        async signup(e: Event) {
            e.preventDefault()
            if (this.valid() && (this.$refs.formSignup as Vue & {validate: () => boolean}).validate()) {
                this.loading = true
                const registerResponse = await this.$store.dispatch('Register', {
                    name: this.name,
                    email: this.email,
                    password: this.password
                })
                this.loading = false
                if (registerResponse.status) {
                    // this.$swal.fire({
                    //     icon: 'success',
                    //     title: 'Success',
                    //     text: registerResponse.response
                    // })
                    this.$router.push({
                        name: "signups"
                    })
                } else {
                    this.alert = true
                    // this.$swal.fire({
                    //     icon: 'error',
                    //     title: 'Oops...',
                    //     text: registerResponse.response.message
                    // })
                }
            }
        },
        valid() {
            return this.password === this.password2;
        },
        backLogin() {
            this.$router.push({
                name: "Login"
            })
        },
        close_alert() {
            this.alert = false;
        }
    }
})
