





































import {
    Vue
} from 'vue-property-decorator'
import SigninTxt from '@/components/molecules/Login/Signin_txt.vue'
import {
    GetToken
} from '@/helpers/Config/Common'

export default Vue.extend({
    components: {
        SigninTxt
    },
    data() {
        return {
            username: '',
            password: '',
            loading: false,
            alert: true,
            alert1: false,
            show1: false,
            rules: {
                required: (value: string) => !!value || 'Required.',
            }

        }
    },
    mounted() {
        if (document.cookie != null && GetToken()) {
            this.$router.push({
                name: "Main"
            })
        }
    },
    methods: {
        async onLogin(e: Event) {
            e.preventDefault()
            this.loading = true;
            const loginResponse = await this.$store.dispatch('retrieveToken', {
                username: this.username,
                password: this.password
            })
            this.loading = false;
            if (loginResponse.status) {
                this.$router.push({
                    name: "Main"
                })
            } else {
                this.alert = false;
                this.alert1 = true;
            }
        },
        forgot() {
            this.$router.push({
                name: 'ForgotPassword'
            });
        },
        close_alert() {
            this.alert = false;
        },
        close_alert1() {
            this.alert1 = false;
        }
    }
})
