































import {
    Vue
} from 'vue-property-decorator'
import SigninTxt from '@/components/molecules/Login/Signin_txt.vue'
import {
    GetToken
} from '@/helpers/Config/Common'

export default Vue.extend({
    components: {
        SigninTxt
    },
    data() {
        return {
            username: '',
            password: '',
            loading: false,
            alert: false,
            show1: false,
            rules: {
                required: (value: string) => !!value || 'Required.',
                email: (value: string) => {
                    const pattern =
                        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                    return pattern.test(value) || "Please enter a valid Email.";
                },
            }

        }
    },
    mounted() {
        if (document.cookie != null && GetToken()) {
            this.$router.push({
                name: "Main"
            })
        }
    },
    methods: {
        async onLogin(e: Event) {
            e.preventDefault()
            this.loading = true;
            const loginResponse = await this.$store.dispatch('retrieveToken', {
                username: this.username,
                password: this.password
            })
            this.loading = false;
            if (loginResponse.status) {
                window.location.replace("/dashboard/main");
                this.$forceUpdate();
                this.$router.push({
                    name: "Main"
                })
            } else {
                this.alert = true;
            }
        },

        forgot() {
            this.$router.push({
                name: 'ForgotPassword'
            });
        },
        close_alert() {
            this.alert = false;
        }
    }
})
